<template>
  <div>
    <el-row type="flex" justify="left" style="flex-wrap: wrap">
      <!--ALL USERS-->
      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8"
              class="pl-2 pr-2 mt-2">
        <v-card>
          <v-card-title class="event_header primary white--text">
            <h3 class="pl-3 white--text">
              Live chat users
            </h3>
          </v-card-title>
          <v-card>
            <div id="chat_users" ref="chatUsers">
              <div v-for="(chat, id) of allChats"
                   :key="id"
                   :class="chatId === id ? 'primary' : ''">
                <el-button type="text"
                           :class="chatId === id ? 'white--text' : ''"
                           @click="openChat(id)">
                  {{ chat.props.userEmail ? ( chat.props.userEmail ) : `Anonymous ( ${id.substring(0, 5)} )` }}
                </el-button>
              </div>
            </div>
          </v-card>
        </v-card>
      </el-col>
      <!--USER EVENTS-->
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8"
              class="pl-2 pr-2 mt-2"
              v-if="userEvents">
        <v-card>
          <v-card-title class="event_header primary white--text">
            <h3 class="pl-3 white--text">
              User Events
            </h3>
          </v-card-title>
          <div ref="userEvents" id="event_messages">
            <el-row v-for="(event, idx) in userEvents"
                    :key="idx"
                    justify="left">
              <el-col :span="6" class="info--text left pr-1">{{ new Date(event.date) | chatTime }}</el-col>
              <el-col :span="18" align="left" class="pb-2">{{ event.name }}</el-col>
            </el-row>
          </div>
        </v-card>
      </el-col>
      <!--LIVE CHAT-->
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8"
              class="pl-2 pr-2 mt-2"
              v-if="userEvents">
        <live-chat :chatId="chatId"
                   :isUserSide="false"
                   :isCollapsed="false">
        </live-chat>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import LiveChat from '@/components/shared/LiveChat'

export default {
  name: 'AdminLiveChat',
  components: {
    LiveChat
  },
  data () {
    return {
      chatId: ''
    }
  },
  methods: {
    fetchAllChats () {
      this.$store.dispatch('fetchAllChats')
    },
    openChat (chatId) {
      // CLOSE OLD CHAT
      if (this.chatId && this.chatId !== chatId) {
        this.$store.dispatch('setChatProp', {
          chatId: this.chatId,
          props: 'isCollapsedAdmin',
          value: true
        })
        console.log(`Chat ${this.chatId} closed`)
      }
      // OPEN NEW CHAT
      this.$store.dispatch('setChatProp', {
        chatId: chatId,
        props: 'isCollapsedAdmin',
        value: false
      })
      this.$store.dispatch('initializeChat', {chatId: chatId})
      this.chatId = chatId
      console.log(`Chat ${chatId} opened`)
    },
    scrollEventsToBottom () {
      if (this.$refs.userEvents) {
        let events = this.$refs.userEvents
        events.scrollTop = events.scrollHeight
      }
    }
  },
  computed: {
    allChats () {
      return this.$store.getters.allChats
    },
    userEvents () {
      return this.$store.getters.userEvents
    }
  },
  watch: {
    userEvents () {
      this.$nextTick(function () {
        this.scrollEventsToBottom()
      })
    }
  },
  created () {
    this.fetchAllChats()
  }
}
</script>

<style scoped>
  .event_header {
    margin-bottom: 1px;
    padding-bottom: 12px;
  }

  #event_messages {
    width: 100%;
    height: 420px;
    overflow: scroll;
  }

  #chat_users {
    width: 100%;
    height: 420px;
    overflow: scroll;
  }
</style>



